export function isWebpSupported() {
    if (!document.createElement('canvas').toDataURL('image/webp').indexOf('data:image/webp') === 0)
        document.documentElement.classList.remove("webp")
}
export function getPropertyValue(el, prop) {
    return parseInt(getComputedStyle(el)[prop])
}
const spoilerIsOpenClassName = "_spoiler-open"
const spoilerIsSlidingClassName = "_spoiler-is-sliding"
const spoilerIsSlidingUpClassName = `${spoilerIsSlidingClassName}-up`
const spoilerIsSlidingDownClassName = `${spoilerIsSlidingClassName}-down`
const spoilerIsInitClassName = "_spoiler-init"
const DEFAULT_DURATION = 500
export function slideUp(el, duration = DEFAULT_DURATION) {
    if (!el) return
    if (el.classList.contains(spoilerIsSlidingClassName)) return
    el.classList.remove(spoilerIsOpenClassName)
    el.classList.add(spoilerIsSlidingClassName)
    el.classList.add(spoilerIsSlidingUpClassName)
    el.style.transitionProperty = "height, margin, padding" /* [1.1] */
    el.style.transitionDuration = duration + "ms" /* [1.2] */
    el.style.boxSizing = "border-box" /* [2] */
    el.style.height = el.offsetHeight + "px" /* [3] */
    el.offsetHeight // !!!
    el.style.height = 0 /* [4] */
    el.style.paddingTop = 0 /* [5.1] */
    el.style.paddingBottom = 0 /* [5.2] */
    el.style.marginTop = 0 /* [6.1] */
    el.style.marginBottom = 0 /* [7.2] */
    el.style.overflow = "hidden" /* [7] */
    window.setTimeout(() => {
        // el.style.display = "none"; /* [8] */
        el.hidden = true
        el.style.removeProperty("height") /* [9] */
        el.style.removeProperty("padding-top") /* [10.1] */
        el.style.removeProperty("padding-bottom") /* [10.2] */
        el.style.removeProperty("margin-top") /* [11.1] */
        el.style.removeProperty("margin-bottom") /* [11.2] */
        el.style.removeProperty("overflow") /* [12] */
        el.style.removeProperty("transition-duration") /* [13.1] */
        el.style.removeProperty("transition-property") /* [13.2] */
        el.classList.remove(spoilerIsSlidingClassName)
        el.classList.remove(spoilerIsSlidingUpClassName)
    }, duration)
}
export function slideDown(el, duration = DEFAULT_DURATION) {
    if (!el) return
    if (el.classList.contains(spoilerIsSlidingClassName)) return
    el.classList.add(spoilerIsSlidingClassName)
    el.classList.add(spoilerIsSlidingDownClassName)
    el.hidden = false
    const height = el.offsetHeight
    el.style.transitionDuration = duration + "ms"
    el.style.transitionProperty = "height, margin, padding"
    el.style.height = 0 + "px"
    el.style.marginTop = el.style.marginBottom = el.style.paddingTop = el.style.paddingBottom = 0
    el.offsetHeight
    el.style.height = height + "px"
    el.style.overflow = "hidden"
    el.style.removeProperty("margin-top")
    el.style.removeProperty("margin-bottom")
    el.style.removeProperty("padding-top")
    el.style.removeProperty("padding-bottom")
    setTimeout(() => {
        // for (const prop in props) {
        //     el.style.removeProperty(prop);
        // }
        el.style.removeProperty("transition-duration")
        el.style.removeProperty("transition-property")
        el.style.removeProperty("height")
        el.style.removeProperty("overflow")
        el.classList.remove(spoilerIsSlidingClassName)
        el.classList.remove(spoilerIsSlidingDownClassName)
        el.classList.add(spoilerIsOpenClassName)
    }, duration)
}
export function slideToggle(el, duration = DEFAULT_DURATION) {
    if (!el) return
    if (el.hidden) {
        slideDown(el, duration)
    } else {
        slideUp(el, duration)
    }
}

function rgba2rgb(background, color) {
    const alpha = color[3]

    return [Math.floor((1 - alpha) * background[0] + alpha * color[0] + 0.5),
    Math.floor((1 - alpha) * background[1] + alpha * color[1] + 0.5),
    Math.floor((1 - alpha) * background[2] + alpha * color[2] + 0.5)]
}
export function getOffsetFromTop(el) {
    if (!el) return 0
    return el.offsetTop + getOffsetFromTop(el.offsetParent)
}
export function debounce(func, timeout = 200) {
    let timer
    return (...args) => {
        clearTimeout(timer)
        timer = setTimeout(() => func.apply(this, args), timeout)
    }
}
// remember the lasted args
export function executeAndDebounce(fn, timeout = 200) {
    let isThrottled = false, savedThis, savedArgs
    function wrapper() {
        savedArgs = arguments
        if (isThrottled) return
        isThrottled = true
        fn.apply(this, arguments)
        setTimeout(() => {
            isThrottled = false
            if (savedArgs) {
                wrapper.apply(savedThis, savedArgs)
                savedArgs = savedThis = null
            }
        }, timeout);
    }
    return wrapper
}
export function throttle(fn, timeout = 200) {
    let isThrottled = false
    let savedThis
    let savedArgs
    function wrapper() {
        if (isThrottled) return
        isThrottled = true
        fn.apply(this, arguments)
        setTimeout(() => {
            isThrottled = false
            if (savedArgs) {
                wrapper.apply(savedThis, savedArgs)
                savedArgs = savedThis = null
            }
        }, timeout);
    }
    return wrapper
}
export function stickyHeader(selector, stickyClassName = '_sticky') {
    const lowerHeader = document.querySelector(selector)
    const mainContent = document.querySelector('.intro__main-content') || document.querySelector('.privacy-policy')
    const originalY = getOffsetFromTop(lowerHeader)
    function toggleStickyClass() {
        if (window.innerWidth < 992) {
            lowerHeader.classList.remove(stickyClassName)
            mainContent.style.marginTop = ''
            return
        }
        if (window.scrollY >= originalY) {
            lowerHeader.classList.add(stickyClassName)
            // this is crucial to add the top margin to the el
            // that follows the sticky one 
            // otherwise: 
            // 1 the following el will be partially hidden by the sticky el
            // 2 there will be scrolling issues
            // like overshooting after pressing the link for the 1st time
            // because the scroll position is calculated assuming the 
            // dinamically sticky el will be in the flow but by the time 
            // the page has reached the calculated Y coordinate it is 
            // removed from the flow and becase sticky. For similar reason 
            // scrolling to the topmost link is buggy because scroll position
            // is calculated assuming the header will still be sticky
            // but by the time the page stops scrolling is it not
            mainContent.style.marginTop = `${lowerHeader.offsetHeight}px`
        } else {
            lowerHeader.classList.remove(stickyClassName)
            mainContent.style.marginTop = ''
        }
    }
    toggleStickyClass()
    window.addEventListener('scroll', executeAndDebounce(toggleStickyClass, 100))
}
const additionalOffsetTop = 0
export function scrollToTarget(e) {
    let fixedEl
    if (window.innerWidth > 992) fixedEl = document.querySelector('.lower-header')
    else fixedEl = document.querySelector('.upper-header')

    const link = e.target.closest('a')
    if (!link) return
    const href = link.getAttribute('href')
    const match = href.match(/\/?(#.+)/)
    if (!match) return
    const hash = match[1]
    e.preventDefault()
    let el = document.querySelector(hash)
    if (!el) return
    let offsetY = getOffsetFromTop(el) - fixedEl.offsetHeight - additionalOffsetTop
    const newLocation = window.location.hash.substring(0, window.location.hash.indexOf('#')) + hash
    window.location = newLocation
    window.scrollTo(0, offsetY)
}
