import Schema from 'async-validator';
import Swal from 'sweetalert2'

export function validate() {
    const form = document.querySelector('.appointment__form form')
    if (!form) return

    const descriptor = {
        name: {
            type: 'string',
            required: true,
            asyncValidator: (rule, value) => {
                // console.log('rule', rule)
                // console.log('value', value)
                // console.log('cb', cb)
                // console.log('source', source)
                // console.log('options', options)
                return new Promise((resolve, reject) => {
                    const minLength = 2
                    const maxLength = 100
                    if (!value) reject("Вы забыли указать свое имя")
                    if (/\d/.test(value)) reject('Имя не должно содержать цифр')
                    if (value.length < minLength) {
                        reject(`Имя должно быть не короче ${minLength} букв`);  // reject with error message
                    } else if (value.length > maxLength) {
                        reject(`Имя должно быть не длиннee ${minLength} букв`)
                    } else {
                        resolve()
                    }
                });
            },
        },
        phone: {
            type: 'string',
            required: true,
            asyncValidator: (rule, value, cb, source, options) => {
                return new Promise((resolve, reject) => {
                    value = form.querySelector(`[name='${rule.field}']`).inputmask.unmaskedvalue()
                    if (!value) reject("Вы забыли указать номер телефона")
                    else if (value.length < 10) {
                        reject('Номер телефона короткий, нужно 10 цифр');  // reject with error message
                    } else {
                        resolve();
                    }
                });
            },
        },
        'consultation-type': {
            type: 'integer',
            required: true,
            asyncValidator: (rule, value, cb, source, options) => {
                return new Promise((resolve, reject) => {
                    if (!value) reject("Выберите тип консультации")
                    else {
                        resolve();
                    }
                });
            },
        },
        'privacy-policy-agreement': {
            type: 'integer',
            required: true,
            asyncValidator: (rule, value, cb, source, options) => {
                return new Promise((resolve, reject) => {
                    if (!value) reject("Пожалуйста, согласитесь с условиями")
                    else {
                        resolve();
                    }
                });
            },
        },

    };
    const validator = new Schema(descriptor);

    form.addEventListener('submit', e => {

        e.preventDefault()

        const formData = Object.fromEntries(new FormData(form))

        validator.validate(formData, (errors, fields) => {
            // console.log('validator.validate', formData)
            if (errors) {
                // validation failed, errors is an array of all errors
                // fields is an object keyed by field name with an array of
                // errors per field
                return handleErrors(errors, fields);
            }
            // validation passed
            handleErrors(errors, fields)
            Swal.fire({
                title: 'Спасибо за заявку, ' + formData.name,
                text: "Мы вам немедленно перезвоним",
                icon: 'success'
            })
        });

        //   // PROMISE USAGE
        //   validator.validate(formData).then(() => {
        //     console.log('validator.validate promise')
        //     // validation passed or without error message
        //   }).catch(({ errors, fields }) => {
        //     return handleErrors(errors, fields);
        //   });
    })
    function handleErrors(errors, fields) {
        // console.log('errors', errors)
        // console.log('fields', fields)
        for (const input of form.querySelectorAll('[name]:not(.select-shadow-wrapper)')) {
            const row = input.closest('.appointment__form-row')
            const errorContainer = row.querySelector('.appointment__form-error-message')
            const field = fields[input.getAttribute('name')]
            errorContainer.textContent = ''
            if (!errors) {
                row.classList.remove('_error')
                row.classList.add('_valid')
            } else if (field) {
                row.classList.remove('_valid')
                row.classList.add('_error')
                for (const el of field) {
                    errorContainer.textContent += el.message
                }
            } else {
                row.classList.remove('_error')
                row.classList.add('_valid')
            }
        }

    }
}
