function burger() {
    const burger = document.querySelector(".icon-menu")
    if (!burger) return
    const navContainer = document.querySelector('.lower-header__container')
    const nav = navContainer.querySelector('.lower-header__menu')
    const popup = document.querySelector(".burger-popup .popup")
    const closeBtn = popup.querySelector(".popup-close-button")
    let isPopupOpen = false
    const lock = document.querySelector('._lock')
    function togglePopup() {
        if (isPopupOpen) closePopup()
        else openPopup()
    }
    function openPopup() {
        popup.querySelector('.popup-body').insertAdjacentElement('afterbegin', nav)
        popup.classList.add("popup-visible")
        document.body.style.overflow = 'hidden'
        lock.classList.add('_active')
        isPopupOpen = true
    }
    function closePopup() {
        navContainer.insertAdjacentElement('afterbegin', nav)
        lock.classList.remove('_active')
        document.body.style.overflow = ''
        popup.classList.remove("popup-visible")
        isPopupOpen = false
    }
    burger.addEventListener("click", togglePopup)
    closeBtn.addEventListener("click", togglePopup)
    popup.addEventListener('click', function (e) {
        if (e.target.closest('a')) {
            closePopup()
        }
    })
}
burger()
// import { phonebackForm } from "./modules/form.js"
// phonebackForm()
function sliderPopup() {
    let activePopup
    let activeSlide
    let activeTitle
    document.addEventListener("click", function (e) {
        // console.log("slide click", e.target)
        const target = e.target.closest(".personal-stories .swiper-slide")
        if (!target) return
        activeSlide = target
        // console.log("click on slide, opening popup")
        activeTitle = activeSlide.querySelector(".slide__title")
        const slider = activeSlide.closest(".personal-stories__slider")
        activePopup = activeSlide.querySelector(".popup")
        activePopup.classList.toggle("popup-visible")
        activePopup
            .querySelector(".popup-body .text-content")
            .insertAdjacentElement("afterbegin", activeTitle)
        slider.insertAdjacentElement("beforeend", activePopup)
        document.body.classList.toggle("_lock")
    })
    document.addEventListener("click", function (e) {
        // console.log("popup click", e.target)
        if (!e.target.closest('.personal-stories')) return
        if (e.target.closest(".popup") && !e.target.closest(".popup-body")) {
            // console.log("clicked on popup, closing it")
            activePopup.classList.toggle("popup-visible")
            activeSlide.insertAdjacentElement("beforeend", activeTitle)
            activeSlide.insertAdjacentElement("beforeend", activePopup)
            document.body.classList.toggle("_lock")
        }
    })
}
sliderPopup()

