import { getOffsetFromTop } from "./functions.js"

const doScrollToTabsContainer = true
const scrollTopOffset = 40
const scrollTopMaxWidth = 550

export function init() {
    const tabsContainer = document.querySelector("[data-tabs-container]")
    if (!tabsContainer) return
    const tabs = tabsContainer.querySelectorAll("[data-tab]")

    const tabBar = document.createElement("div")
    tabBar.setAttribute("data-tab-bar", "")
    let hasActiveTab = false
    let tabHeadings = []
    for (let i = 1; i <= tabs.length; i++) {
        const tab = tabs[i - 1]
        const tabHeading = tab.querySelector("[data-tab-button]")
        tabHeading.setAttribute("data-tab-index", i)
        tabHeadings.push(tabHeading)
        tabBar.append(tabHeading)
    }
    if (!hasActiveTab) tabHeadings[0].setAttribute("data-tab-active", "")
    tabsContainer.insertAdjacentElement("afterbegin", tabBar)
    tabBar.addEventListener("click", (e) => switchButton(e.target))
    initTabs(tabBar)
}
function switchButton(el) {
    const activeTabButton = el.closest("[data-tab-button]")
    doSwitch(activeTabButton)
    if (doScrollToTabsContainer & window.innerWidth <= scrollTopMaxWidth) {
        const activeTab = document.querySelector(`[data-tabs]`)
        const activeTabTopOffset = getOffsetFromTop(activeTab)
        let fixedEl
        if (window.innerWidth > 992) fixedEl = document.querySelector('.lower-header')
        else fixedEl = document.querySelector('.upper-header')
        window.scrollTo(0, (activeTabTopOffset - scrollTopOffset - fixedEl.offsetHeight))
    }
}
function initTabs(tabBar) {
    // const tabBar = el.closest("[data-tabs-bar]")
    // const tabButton = el.closest("button")
    const activeTabButton = tabBar.querySelector("[data-tab-active]")
    doSwitch(activeTabButton)
}
function doSwitch(activeTabButton) {
    const tabBar = activeTabButton.closest("[data-tab-bar]")
    const tabsContainer = tabBar.closest("[data-tabs-container]")
    const tabsHolder = Array.prototype.filter.call(tabsContainer.children, (c) =>
        c.matches("[data-tabs]")
    )[0]
    const tabs = Array.prototype.filter.call(tabsHolder.children, (c) => c.matches("[data-tab]"))
    const tabIndex = activeTabButton.dataset.tabIndex
    const buttons = Array.prototype.filter.call(tabBar.children, (c) =>
        c.matches("[data-tab-button]")
    )
    resetActiveTab(tabs, tabIndex)
    resetActiveButton(activeTabButton, buttons)
}
// const FADE_TIMEOUT = 1000
function resetActiveTab(tabs, tabIndex) {
    const tabEl = document.querySelector("[data-tab]")
    let tabFadeTimeout = getComputedStyle(tabEl).transitionDuration
    const to = parseFloat(tabFadeTimeout) * 1000
    // console.log("tab timeout ", to)
    tabs.forEach((tab) => {
        if (tab.hasAttribute("data-tab-active")) {
            // tab.classList.add("tab-is-fading-out")
            tab.removeAttribute("data-tab-active")
            setTimeout(() => {
                // tab.classList.remove("tab-is-fading-out")
            }, to)
        }
    })
    if (tabs[tabIndex - 1]) {
        const activeTab = tabs[tabIndex - 1]
        activeTab.setAttribute("data-tab-active", "")
        // activeTab.classList.add("tab-is-fading-in")
        setTimeout(() => {
            // activeTab.classList.remove("tab-is-fading-in")
        }, to)
    }
}
function resetActiveButton(tabButton, buttons) {
    buttons.forEach((button) => {
        button.removeAttribute("data-tab-button-active")
        // button.removeAttribute("data-tab-active")
        // button.dataset.tabActive = false
    })
    tabButton.setAttribute("data-tab-button-active", "")
}
